@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}
*:focus {
  outline: 0;
}
html,
body,
#root {
  height: 100%;
}
body {
  -webkit-font-smoothing: antialiased;
}
body,
input,
button {
  font: 14px "Roboto", sans-serif;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
button {
  cursor: pointer;
}

.Master {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #fff;
}

.Header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  background-color: #fff;
  padding-left: 30px;
  padding-right: 30px;
  border-bottom: solid 0.5px #ddd;
}

.divImgLogo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.ImgLogo {
  width: 50px;
  height: 50px;
}
.FontTextLogo {
  font-size: 30px;
  font-family: "Roboto";
  font-weight: bold;
  color: #777;
}

.InputDivSearch {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 30px;
  border-radius: 15px;
  padding: 0px 10px;
  color: #999;
  border: solid 0.5px #ddd;
}

.InputSearch {
  border: none;
  width: 120px;
  color: #999;
  padding-left: 5px;
}

::-webkit-input-placeholder {
  color: #ddd;
}

.BodyHeader {
  width: 900px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-top: 30px;
  background-color: #fff;
}

.DivTextHeader {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 600px;
  height: 200px;
  background-color: #fff;
}

.TextTitleName {
  font-size: 30px;
  font-family: "Roboto";
  color: #444;
}
.TextSubTitleName {
  font-size: 13px;
  font-family: "Roboto";
  font-weight: bold;
  color: #444;
  margin-top: 10px;
}
.TextDescriptionName {
  font-size: 20px;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  font-weight: normal;
  color: #444;
  margin-top: 10px;
  margin-left: 10px;
}
.DivImgHeader {
  width: 300px;
  border-left: solid 1px #ccc;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 30px 0px;
}
.ImgHeaderPessoal {
  width: 200px;
  height: 200px;
  border-radius: 100px;
}
.DivSocial {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background-color: #fff;
  width: 900px;
  height: 60px;
  margin: 10px 20px;
}

.DivSocial2 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.ImgSocial {
  width: 40px;
  height: 40px;
}

.DivFrase {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 40px;
  background-color: #fff;
}

.DivCard {
  display: flex;
  flex-direction: row;
  width: 900px;
  height: 285px;
  border: solid 1px #ccc;
  margin-top: 20px;
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0px 20px 15px -25px rgba(0, 0, 0, 0.78);
}

.DivImagem {
  width: 410px;
  height: 265px;
  background-color: #7159c1;
  margin: 10px;
}

.DivTextCard {
  width: 625px;
  height: 265px;
  background-color: #9999;
  margin: 10px;
}

